import { NextResponse } from "next/server";

const TENANT_ID = process.env.TENANT_ID!;
const CLIENT_ID = process.env.CLIENT_ID!;
const CLIENT_SECRET = process.env.CLIENT_SECRET!;

export async function GET(req: Request) {
  try {
    const url = new URL(req.url);
    const userId = url.searchParams.get("userId"); // optionnel

    // 1️⃣ Récupérer un token OAuth2
    const tokenRes = await fetch(
      `https://login.microsoftonline.com/${TENANT_ID}/oauth2/v2.0/token`,
      {
        method: "POST",
        headers: { "Content-Type": "application/x-www-form-urlencoded" },
        body: new URLSearchParams({
          client_id: CLIENT_ID,
          scope: "https://graph.microsoft.com/.default",
          client_secret: CLIENT_SECRET,
          grant_type: "client_credentials",
        }),
      }
    );

    const tokenText = await tokenRes.text();
    if (!tokenRes.ok) {
      return NextResponse.json({ error: "Token error", details: tokenText }, { status: 500 });
    }

    const token = JSON.parse(tokenText);

    // 2️⃣ Si aucun userId : lister tous les utilisateurs
    if (!userId) {
      const usersRes = await fetch("https://graph.microsoft.com/v1.0/users", {
        headers: { Authorization: `Bearer ${token.access_token}` },
      });

      const usersText = await usersRes.text();
      if (!usersRes.ok) {
        return NextResponse.json({ error: "Graph API error", details: usersText }, { status: 500 });
      }

      let users;
      try {
        users = JSON.parse(usersText);
      } catch {
        return NextResponse.json({ error: "Graph response not JSON", raw: usersText }, { status: 500 });
      }

      // retourner id et displayName seulement
      const simplified = users.value.map((u: any) => ({
        id: u.id,
        displayName: u.displayName,
        mail: u.mail || u.userPrincipalName,
      }));

      return NextResponse.json({ success: true, users: simplified });
    }

    // 3️⃣ Si userId fourni : récupérer le OneDrive de cet utilisateur
    const driveRes = await fetch(
      `https://graph.microsoft.com/v1.0/users/${userId}/drive/root/children`,
      {
        headers: { Authorization: `Bearer ${token.access_token}` },
      }
    );

    const driveText = await driveRes.text();
    if (!driveRes.ok) {
      return NextResponse.json({ error: "Graph API error", details: driveText }, { status: 500 });
    }

    let drive;
    try {
      drive = JSON.parse(driveText);
    } catch {
      return NextResponse.json({ error: "Graph response not JSON", raw: driveText }, { status: 500 });
    }

    return NextResponse.json({ success: true, data: drive });

  } catch (err: any) {
    return NextResponse.json({ error: "Server crash", details: err.message }, { status: 500 });
  }
}
